.flip-container {
  perspective: 1000;
  display: inline-block;
}
.flip-container:hover .logo {
  transform: rotateY(180deg);
}
.logo,
.front,
.back {
  width: 150px;
  height: 70px;
}
.logo {
  transition: 0.5s;
  transform-style: preserve-3d;
  position: relative;
}
.front,
.back {
  backface-visibility: hidden;
  position: absolute;
}
.front {
  z-index: 1;
  transform: rotateY(180deg);
}
.back {
  z-index: 2; /* back side, placed above front */
  transform: rotateY(0deg);
}
